<template>

  <div class="right">

    <div class="title">

      <span class="popup-head"></span>

      <span class="popup-desc">

        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.

      </span>

    </div>

    <!-- VIEW MODE -->
    <div v-if="!edit" key="store_editor">
      <span class="popup-head mt30"></span>
      <div class="shop">
        <div class="item" v-for="store in currentUserStores" :key="store.name" :class="{'hover': hoveredStore && hoveredStore.name === store.name}" @click="hoveredStore = store" @mouseover="hoveredStore = store" @mouseleave="hoveredStore = null">
          <span class="name">
              {{ store.name }}
          </span>
          <span class="num">
              {{ store.count }} {{ store.count | pluralize(',,') }}
          </span>
          <img :src="store.logo">
          <span class="red-btn dell" @click.prevent="onDeleteStore(store)"></span>
        </div>
      </div>
      <a href="#" class="btn-add" @click.prevent="edit = 'store'"> </a>

      <span class="popup-head"></span>
      <div class="category" v-if="currentUserCategories.length">
        <a href="#" v-for="(category, index) in currentUserCategories" :key="index" @click.prevent="hoveredStore = category"
          :class="{'hover': hoveredStore === category}"
          @mouseover="hoveredStore = category" @mouseleave="hoveredStore = null">
          {{ category }}
          <span @click="onDeleteCategory(category)"></span>
        </a>
      </div>
      <a href="#" class="btn-add" @click.prevent="edit = 'category'"> </a>
    </div>

    <!-- EDIT STORES -->
    <div v-else-if="edit === 'store'" key="store_editor">
      <span class="popup-head"> </span>
      <div class="lk-form">
        <div class="lk-row">
          <div class="input-block search">
            <input type="text" placeholder="  " v-model="searchStoreString">
          </div>
        </div>
        <div class="lk-form-shop-wrap">
          <div class="lk-form-shop">
            <div class="lk-row" v-for="(store, index) in filteredStores" :key="index" :title="store.place">
              <div class="check-box">
                <input type="checkbox" :name="'store_' + index" :id="'store_' + index" v-model="store.model">
                <label :for="'store_' + index">{{ store.name }}</label>
              </div>
            </div>
          </div>
        </div>
        <div class="lk-row mt30">
            <a href="#" class="red-btn edit" @click.prevent="onSaveStores"></a>
            <a href="#" class="red-btn cancel" @click.prevent="edit = false"></a>
        </div>
      </div>
    </div>

    <!-- EDIT CATEGORIES -->
    <div v-else-if="edit === 'category'" key="store_editor">
        <span class="popup-head"> </span>
        <div class="category category-select">
          <a href="#" v-for="(category, index) in categories" :key="index"
              @click.prevent="category.model = !category.model"
              :class="{'act': category.model}">{{ category.name }}</a>
        </div>
        <div class="lk-form">
          <div class="lk-row mt30">
            <a href="#" class="red-btn edit" @click.prevent="onSaveCategories"></a>
            <a href="#" class="red-btn cancel" @click.prevent="edit = false"></a>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
const TAGS_REQUEST_TIMEOUT = 1000;
import { mapGetters } from "vuex";
export default {
  name: "AppPopupFavorites",
  data() {
    return {
      holding: false,
      edit: false,
      stores: null,
      searchStoreString: "",
      hoveredStore: null,
      categories: null
    };
  },
  watch: {
    edit() {
      this.searchStoreString = "";
      this.stores = this.storeList.map(store => {
        let value = this.$options.filters["tagExist"](store.name);
        store.origin = value;
        store.model = value;
        return store;
      });
      this.categories = this.storeCategories.map(category => {
        let value = this.$options.filters["tagExist"](category);
        return {
          name: category,
          origin: value,
          model: value
        };
      });
    }
  },
  computed: {
    ...mapGetters([
      "storeList",
      "storeCategories",
      "currentUserCategories",
      "currentUserStores",
      "currentUser",
      "remoteConfig"
    ]),
    filteredStores() {
      return this.stores.filter(
        store =>
          this.searchStoreString.length
            ? store.name
                .toLowerCase()
                .indexOf(this.searchStoreString.toLowerCase()) === 0
            : true
      );
    }
  },
  methods: {
    onSaveStores() {
      if (this.holding) return;
      let tags = {
        add: this.stores
          .filter(item => item.model && item.model !== item.origin)
          .map(item => item.name),
        remove: this.stores
          .filter(item => !item.model && item.model !== item.origin)
          .map(item => item.name)
      };
      this.holding = true;
      this.$store.dispatch("changeTags", tags).then(() => {
        this.completeFavorites();
        setTimeout(() => {
          this.$store.dispatch("fetchTags").then(() => {
            this.edit = false;
            this.holding = false;
          });
        }, TAGS_REQUEST_TIMEOUT);
      });
    },
    onDeleteStore(store) {
      if (this.holding) return;
      this.holding = true;
      this.$store
        .dispatch("changeTags", {
          remove: [store.name]
        })
        .then(() => {
        setTimeout(() => {
          this.$store.dispatch("fetchTags").then(() => {
            this.holding = false;
          });
        }, TAGS_REQUEST_TIMEOUT);
        });
    },
    onSaveCategories() {
      if (this.holding) return;
      let tags = {
        add: this.categories
          .filter(item => item.model && item.model !== item.origin)
          .map(item => item.name),
        remove: this.categories
          .filter(item => !item.model && item.model !== item.origin)
          .map(item => item.name)
      };

      this.holding = true;
      this.$store.dispatch("changeTags", tags).then(() => {
        this.completeFavorites();
        setTimeout(() => {
          this.$store.dispatch("fetchTags").then(() => {
            this.edit = false;
            this.holding = false;
          });
        }, TAGS_REQUEST_TIMEOUT);
      });
    },
    onDeleteCategory(category) {
      if (this.holding) return;
      this.holding = true;
      this.$store
        .dispatch("changeTags", {
          remove: [category]
        })
        .then(() => {
        setTimeout(() => {
          this.$store.dispatch("fetchTags").then(() => {
            this.holding = false;
          });
        }, TAGS_REQUEST_TIMEOUT);
        });
    },
    completeFavorites(){

      if(this.stores.length > 2 && this.categories.length > 1) {

        console.log(this.remoteConfig.favoriteTag);

        this.remoteConfig.favoriteTag && this.$store.dispatch("changeTags", { add: [ this.remoteConfig.favoriteTag ] });

      }

    }
  }
};
</script>
